import "whatwg-fetch";

export default class API {

    public static get (url:string) : Promise<Response> {
        return (window as any).fetch(url, {
            headers: {
                'x-token': (window as any).token
            },
            credentials: 'same-origin'
        });
    }

    public static post (url:string, data:any) : Promise<Response> {
        return fetch(url, {
            method: 'post',
            headers: {
                'x-token': (window as any).token,
                'content-type': 'application/json'
            },
            body: JSON.stringify(data)
        });
    }

    public static delete () {

    }

    public static put () {

    }

}
